import React from 'react'
import { Link } from 'react-router-dom'
import './Topbar.css'

const Topbar = () => {
    return (
        
        <div className="topmenu">
        <div className="menu">
        <Link to="/">Home</Link>
            <Link to="/mounting" exact>Mounting</Link>
            <Link to="/updating" exact>Updating</Link>
            <Link to="/unmounting">Unmounting</Link>
            
            </div>
        </div>
        
    )
}

export default Topbar
