import React from 'react'
import Footer from '../Footer/Footer'
import Header from '../Header/Header'
import Topbar from '../Topbar/Topbar'
import './Updating.css'


const Updating = () => {
    return (
        <div>

        <Topbar/>

        <Header/>

        <div className="mounting">


                <h1 className="mountingheading">The Component Lifecycle</h1>

                <p style={{ marginTop: "20px", fontSize: "20px" }}>Each component has several “lifecycle methods” that you can override to run code at particular times in the process. You can use this lifecycle diagram as a cheat sheet. In the list below, commonly used lifecycle methods are marked as bold. The rest of them exist for relatively rare use cases.</p>


                <h1>2. Updating</h1>

                <h3 className="desc">An update can be caused by changes to props or state. These methods are called in the following order when a component is being re-rendered:</h3>

                <ol>

                    <b><li >static getDerivedStateFromProps()</li>
                        <li>shouldComponentUpdate()</li>
                        <li>render()</li>
                        <li>componentDidUpdate()</li>
                        <li>componentDidUpdate()</li>
                       <li> getSnapshotBeforeUpdate()</li>
                    </b>

                </ol>

                <p>Note: These methods are considered legacy and you should avoid them in new code:
                UNSAFE_componentWillUpdate()
UNSAFE_componentWillReceiveProps()
                </p>


            </div>



        <Footer/>

        
            
        </div>
    )
}

export default Updating
