import React from 'react';
import { Route, BrowserRouter as Router, Switch } from 'react-router-dom';
import Header from './Header/Header';
import Home from './Homepage/Home';
import Mounting from './Mounting/Mounting';
import Unmounting from './Unmounting/Unmounting';
import Updating from './Updating/Updating';

function App() {
  return (
    <div className="App" >

    

      <Router>

      <Switch>

        <Route path="/simpli-learn" exact> <Home /> </Route>
        <Route path="/mounting" exact> <Mounting /> </Route>
        <Route path="/updating" exact> <Updating /> </Route>
        <Route path="/unmounting" exact> <Unmounting /> </Route>

        </Switch>

      </Router>

    </div>
  );
}

export default App;
